import { useState } from 'react'
import { subscribe } from '../actions/newsletterActions'

export default function Footer() {
    const [newsletterMessage, setNewsletterMessage] = useState('');
    const handleNewsletterSubmit = async (e) => {
        e.preventDefault()
        let email = await subscribe(e.target.email.value)
        email && setNewsletterMessage(`added to newsletter`) 
    }

    return (
        <>
        <section className='footer'>
            <ul>
                <h3>POLICY</h3>
                <li>Terms of Service</li>
                <li>Refund Policy</li>
                <li>Shipping</li>
                <li>FAQ</li>
            </ul>
            <ul className='subscribe'>
                <h3>REWARDS</h3>
                <form onSubmit={handleNewsletterSubmit}>
                    <p>Sign up for the latest news, offers and styles and receive 10% off your first order.</p>
                    <input type='email' name='email' placeholder='Email'/>
                    <input type='submit' value='Submit' className='btn btn-outline-secondary btn-sm'/>
                </form>
                {newsletterMessage && (<p>{newsletterMessage}</p>) }
            </ul>
            <ul className='socials'>
                <h3>CONNECT</h3>
                <li>#KAVAGLAM</li>
            </ul>
        </section>
        <div className='copyright'>
                <span>© copyright 2021, Kavaglam Canada</span>
        </div>
        </>
    )
}
